/**
 * flex item
 */

 .flex {
  display: flex;
  flex-flow: row wrap; /* flex-direction: row; + flex-wrap: wrap; */
  
  @for $i from 0 through 12 {
    .item.item-#{$i} {
      flex-grow: #{$i};
      flex-basis: 0;
    }
  }

  @include responsive("xbig") {
    @for $i from 0 through 12 {
      .item.item-xb-#{$i} {
        flex-grow: #{$i};
        flex-basis: 0;
      }
    }
  }
  @include responsive("large") {
    @for $i from 0 through 12 {
      .item.item-l-#{$i} {
        flex-grow: #{$i};
        flex-basis: 0;
      }
    }
  }
  @include responsive("medium") {
    @for $i from 0 through 12 {
      .item.item-m-#{$i} {
        flex-grow: #{$i};
        flex-basis: 0;
      }
    }
  }
  @include responsive("small") {
    @for $i from 0 through 12 {
      .item.item-s-#{$i} {
        flex-grow: #{$i};
        flex-basis: 0;
      }
    }
  }
}

