/**
 * flex col
 */

$gutter: 20px; // Define columns gutter

.flex {
  .col {
    min-height: 1px;
  }

  @for $i from 0 through 12 {
    $width: 100% / 12 * $i;

    .col-#{$i} {
      flex: 0 0 $width;
      @if $i == 12 {
        padding-left: 0px;
        padding-right: 0px;
      } @else {
        padding-left: $gutter / 2;
        padding-right: $gutter / 2;
      }

      &:first-of-type {
        padding-left: 0px;
      }
      &:last-of-type {
        padding-right: 0px;
      }
    }
  }

  @include responsive("xbig") {
    @for $i from 0 through 12 {
      $width: 100% / 12 * $i;

      .col-xb-#{$i},
      .xb\:col-#{$i} {
        flex: 0 0 $width;
        @if $i == 12 {
          padding-left: 0px;
          padding-right: 0px;
        } @else {
          padding-left: $gutter / 2;
          padding-right: $gutter / 2;
        }

        &:first-of-type {
          padding-left: 0px;
        }
        &:last-of-type {
          padding-right: 0px;
        }
      }
    }
  }

  @include responsive("large") {
    @for $i from 0 through 12 {
      $width: 100% / 12 * $i;

      .col-l-#{$i},
      .lg\:col-#{$i} {
        flex: 0 0 $width;
        @if $i == 12 {
          padding-left: 0px;
          padding-right: 0px;
        } @else {
          padding-left: $gutter / 2;
          padding-right: $gutter / 2;
        }

        &:first-of-type {
          padding-left: 0px;
        }
        &:last-of-type {
          padding-right: 0px;
        }
      }
    }
  }

  @include responsive("medium") {
    @for $i from 0 through 12 {
      $width: 100% / 12 * $i;

      .col-m-#{$i},
      .md\:col-#{$i} {
        flex: 0 0 $width;
        @if $i == 12 {
          padding-left: 0px;
          padding-right: 0px;
        } @else {
          padding-left: $gutter / 2;
          padding-right: $gutter / 2;
        }

        &:first-of-type {
          padding-left: 0px;
        }
        &:last-of-type {
          padding-right: 0px;
        }
      }
    }
  }

  @include responsive("small") {
    @for $i from 0 through 12 {
      $width: 100% / 12 * $i;

      .col-s-#{$i},
      .sm\:col-#{$i}{
        flex: 0 0 $width;
        @if $i == 12 {
          padding-left: 0px;
          padding-right: 0px;
        } @else {
          padding-left: $gutter / 2;
          padding-right: $gutter / 2;
        }

        &:first-of-type {
          padding-left: 0px;
        }
        &:last-of-type {
          padding-right: 0px;
        }
      }
    }
  }
}

/**
 * flex gutter
 */

.flex {
  &.gutter-0 .col{
    padding-left: 0px;
    padding-right: 0px;
  }
  @include responsive("xbig") {
    &.xb\:gutter-0,
    &.gutter-xb-0{
      .col{
        padding-left: 0px;
        padding-right: 0px;
      }
    }
  }
  @include responsive("large") {
    &.lg\:gutter-0,
